/**
 * Golden Rush Bonus Mode
 *
 * Tier role in current implementation:
 * - Mid free-spin tier (triggered by 4 scatters)
 * - Starts with 12 free spins
 * - Uses the shared Golden-Square chain engine from LuckyScapeSlot
 * - Provides bonus-mode identity plus legacy golden-square helper state for display hooks
 */

import { BonusMode } from "./bonusMode.js";

export class GoldenRush extends BonusMode {
  constructor(initialSpins = 12) {
    super(initialSpins);
    this.name = "Golden Rush";
    this.goldenSquares = new Map(); // Map of "x,y"  multiplier value
    this.unlockChance = 0.3; // 30% to unlock per cascade
    this.maxRetriggers = 1;
    this.retriggersUsed = 0;
  }

  /**
   * Apply golden squares to winning positions
   * Golden squares persist and gain multiplier after spin 4
   */
  onCascade(cascadeIndex, grid, winResult) {
    // Create new golden squares at winning positions
    for (const cluster of winResult.clusters) {
      for (const pos of cluster.positions) {
        const key = `${pos.x},${pos.y}`;

        // Golden square starts at 2x
        // But after spin 4, they're worth 3x (getting upgrade)
        const spinNumber = this.spinsCompleted + 1;
        const baseMultiplier = spinNumber > 4 ? 3.0 : 2.0;

        this.goldenSquares.set(key, baseMultiplier);
      }
    }

    // Check for unlocks (golden squares disappearing)
    this._checkForUnlocks();

    // Return base multiplier (golden squares themselves provide 2x or 3x)
    return 1.0;
  }

  /**
   * Check if golden squares unlock (disappear)
   * @private
   */
  _checkForUnlocks() {
    const keysToRemove = [];

    for (const [key, multiplier] of this.goldenSquares) {
      // 30% chance to unlock
      if (Math.random() < this.unlockChance) {
        keysToRemove.push(key);
      }
    }

    keysToRemove.forEach((key) => this.goldenSquares.delete(key));
  }

  /**
   * Retriggers are handled in LuckyScapeSlot
   * Keep method for interface compatibility
   */
  onRetrigger(newScatterCount) {
    return 0;
  }

  /**
   * Get golden square positions and their multipliers
   */
  getGoldenSquares() {
    return this.goldenSquares;
  }

  /**
   * Check if position has a golden square
   */
  hasGoldenSquare(x, y) {
    const key = `${x},${y}`;
    return this.goldenSquares.has(key);
  }

  /**
   * Get multiplier for a position
   */
  getMultiplier(x, y) {
    const key = `${x},${y}`;
    return this.goldenSquares.get(key) || 1.0;
  }

  /**
   * Get display data with golden square status
   */
  getDisplayData() {
    return {
      ...super.getDisplayData(),
      goldenSquareCount: this.goldenSquares.size,
      nextUpgradeIn: Math.max(0, 4 - this.spinsCompleted),
      retriggersRemaining: this.maxRetriggers - this.retriggersUsed,
      unlockChance: Math.round(this.unlockChance * 100),
    };
  }
}

export default GoldenRush;
