/**
 * LuckyScapeSlot - Main game implementation extending BaseSlot
 *
 * Implements:
 * - 5x5 cluster pays with cascading reels
 * - Three distinct bonus modes (Lock & Load, Golden Rush, Cascade Master)
 * - Free spins with persistent state
 * - Multiplier locks and golden squares
 */

import { BaseSlot } from "../../core/baseSlot.js";
import { CascadeDetector } from "./cascadeDetector.js";
import { CascadeEngine } from "./cascadeEngine.js";
import { LUCKY_ESCAPE_CONFIG } from "./config.js";
import { LockAndLoad } from "./bonusModes/lockAndLoad.js";
import { GoldenRush } from "./bonusModes/goldenRush.js";
import { CascadeMaster } from "./bonusModes/cascadeMaster.js";

export class LuckyScapeSlot extends BaseSlot {
  constructor(config = LUCKY_ESCAPE_CONFIG) {
    super(config);
    this.detector = new CascadeDetector();
    this.engine = new CascadeEngine();

    // Game state
    this.currentGrid = CascadeEngine.createEmptyGrid();
    this.cascadeCount = 0;
    this.totalWinFromSpin = 0;
    this.bonusMode = null;
    this.freeSpinsRemaining = 0;
    this.isInFreeSpins = false;

    // Bonus feature-state (Le Bandit-inspired bonus chain mechanics)
    this.goldenSquares = new Set();
    this.rainbowTargetSymbol = null;
    this.rainbowTriggered = false;
    this.cloverSymbolsHit = [];
    this.bucketSymbolsHit = [];
    this.spinSpecialMultiplier = 1;
    this.spinBucketMultiplier = 1;
    this.spinCollectionValue = 0;
    this.chainRoundsTriggered = 0;
    this.bonusEventTimeline = [];
  }

  /**
   * Execute a complete spin with cascades
   *
   * @param {Object} backend - Backend API interface (optional for demo)
   * @param {number} betAmount - Current bet
   * @returns {Promise<Object>} Spin result with win amount and bonus info
   */
  async spin(backend, betAmount = 10) {
    // Generate initial grid (demo mode - no backend needed)
    this.currentGrid = this._generateRandomGrid();
    this.totalWinFromSpin = 0;
    this.cascadeCount = 0;
    this._resetSpinFeatureState();

    if (this._isBanditStyleBonusActive()) {
      this._prepareBonusActivationGrid();
    }

    const cascades = [];

    // Run cascade sequence
    let cascadeIndex = 0;
    const maxCascades = 20;
    let initialWinPositions = new Set();
    let winPositions = new Set();

    while (cascadeIndex < maxCascades) {
      // Detect wins in current grid
      const winResult = this.detector.findWins(this.currentGrid);

      if (winResult.clusters.length === 0) {
        // No more wins
        break;
      }

      // Store initial wins for first cascade
      if (cascadeIndex === 0) {
        initialWinPositions = new Set(winResult.winPositions);
        winPositions = new Set(winResult.winPositions);
      }

      // Track grid before cascade
      const beforeGrid = CascadeEngine.cloneGrid(this.currentGrid);
      let positionsToRemove = this.detector.getSuperCascadeRemovalPositions(
        this.currentGrid,
        winResult,
      );

      if (this._isBanditStyleBonusActive()) {
        this._trackGoldenSquaresFromWins(winResult);
      }

      // Apply bonus mode multipliers if in free spins
      let modeMultiplier = 1;
      if (this.isInFreeSpins && this.bonusMode) {
        modeMultiplier = this.bonusMode.onCascade(
          cascadeIndex + 1,
          this.currentGrid,
          winResult,
        );
      }

      // Lock & Load: locked symbols remain in place (cannot cascade away)
      if (this.isInFreeSpins && this.bonusMode instanceof LockAndLoad) {
        positionsToRemove = this._filterLockedWinPositions(positionsToRemove);
      }

      const beforeWinPositions = new Set(positionsToRemove);

      const positionMultiplier = this._getWinningPositionMultiplier(winResult);

      // Add win to total (with bonus mode multiplier applied)
      this.totalWinFromSpin +=
        winResult.totalWin *
        modeMultiplier *
        positionMultiplier;

      // If all winning symbols are locked, award once and stop cascading
      if (positionsToRemove.size === 0) {
        break;
      }

      // Execute cascade
      const cascadeResult = this.engine.executeCascade(
        this.currentGrid,
        positionsToRemove,
        this.rng,
      );

      // Store cascade animation data
      cascades.push({
        beforeGrid,
        afterGrid: CascadeEngine.cloneGrid(cascadeResult.grid),
        winPositions: beforeWinPositions,
        moveData: cascadeResult.moveData,
      });

      // Update grid for next iteration
      this.currentGrid = cascadeResult.grid;
      this.cascadeCount++;
      cascadeIndex++;

      // Detect wins for next iteration
      const nextWinResult = this.detector.findWins(this.currentGrid);
      winPositions = nextWinResult.winPositions;
    }

    if (this._isBanditStyleBonusActive()) {
      this.totalWinFromSpin += this.spinCollectionValue;
    }

    // Check for scatter symbols (bonus trigger)
    const scatterResult = this.detector.findScatters(this.currentGrid);

    // Initialize bonus if triggered
    let bonusMode = null;
    if (scatterResult.bonusMode && !this.bonusMode) {
      const modeType = scatterResult.bonusMode;
      let initialSpins = 10; // Default for LOCK_AND_LOAD

      if (modeType === "GOLDEN_RUSH") {
        initialSpins = 12;
      } else if (modeType === "CASCADE_MASTER") {
        initialSpins = 12;
      }

      this._initializeBonusMode(modeType, initialSpins);
      bonusMode = {
        type: modeType,
        name: modeType.replace(/_/g, " "),
        initialSpins: initialSpins,
      };
    }

    const totalWin =
      Math.round((this.totalWinFromSpin * betAmount + Number.EPSILON) * 100) /
      100;

    if (this.isInFreeSpins && this.bonusMode) {
      this.bonusMode.onSpinComplete(totalWin);
      this.freeSpinsRemaining = this.bonusMode.remaining;
    }

    // Return structured result for renderer
    return {
      grid: this.currentGrid,
      initialWins: initialWinPositions,
      winPositions: winPositions,
      cascades: cascades,
      totalWin,
      cascadeCount: this.cascadeCount,
      bonusMode: bonusMode,
      scatterCount: scatterResult.count,
      bonusFeatures: this.getBonusFeatureDisplay(),
    };
  }

  /**
   * Detect wins using cluster detection
   * Called by framework
   *
   * @param {number[][]} grid - Game grid
   * @returns {Object} Win detection result
   */
  detectWins(grid) {
    return this.detector.findWins(grid);
  }

  /**
   * Check for bonus trigger and initialize free spins mode
   * Called by framework
   *
   * @param {number[][]} finalGrid - Final grid after all cascades
   * @returns {Object|null} Bonus info or null if no bonus
   */
  getBonus(finalGrid) {
    const scatterResult = this.detector.findScatters(finalGrid);

    if (scatterResult.bonusMode === null) {
      return null;
    }

    // Determine initial free spins count
    let freeSpinsCount = 10; // Default for LOCK_AND_LOAD
    if (scatterResult.bonusMode === "GOLDEN_RUSH") {
      freeSpinsCount = 12;
    } else if (scatterResult.bonusMode === "CASCADE_MASTER") {
      freeSpinsCount = 12;
    }

    // Initialize bonus mode object
    this._initializeBonusMode(scatterResult.bonusMode, freeSpinsCount);

    return {
      type: scatterResult.bonusMode,
      spins: freeSpinsCount,
      scatterCount: scatterResult.count,
    };
  }

  /**
   * Initialize the appropriate bonus mode
   * @private
   */
  _initializeBonusMode(modeType, initialSpins) {
    switch (modeType) {
      case "LOCK_AND_LOAD":
        this.bonusMode = new LockAndLoad(initialSpins);
        break;
      case "GOLDEN_RUSH":
        this.bonusMode = new GoldenRush(initialSpins);
        break;
      case "CASCADE_MASTER":
        this.bonusMode = new CascadeMaster(initialSpins);
        break;
    }

    this.freeSpinsRemaining = initialSpins;
    this.isInFreeSpins = true;
    this.goldenSquares = new Set();
    this._resetSpinFeatureState();
  }

  /**
   * Start a specific bonus mode directly (Bonus Buy)
   *
   * @param {string} modeType - LOCK_AND_LOAD | GOLDEN_RUSH | CASCADE_MASTER
   * @returns {Object} Bonus metadata
   */
  startBonusMode(modeType) {
    if (this.isInFreeSpins) {
      throw new Error("Cannot start bonus buy during free spins");
    }

    const modeSpins = {
      LOCK_AND_LOAD: 10,
      GOLDEN_RUSH: 12,
      CASCADE_MASTER: 12,
    };

    const initialSpins = modeSpins[modeType];
    if (!initialSpins) {
      throw new Error(`Unknown bonus mode: ${modeType}`);
    }

    this._initializeBonusMode(modeType, initialSpins);

    return {
      type: modeType,
      name: modeType.replace(/_/g, " "),
      initialSpins,
    };
  }

  /**
   * Get bonus buy pricing for current config
   *
   * @param {number} betAmount
   * @returns {Object}
   */
  getBonusBuyOffers(betAmount) {
    const multipliers = this.config?.bonusBuy?.multipliers || {};
    const entries = Object.entries(multipliers).map(
      ([modeType, multiplier]) => {
        const cost =
          Math.round((betAmount * Number(multiplier) + Number.EPSILON) * 100) /
          100;
        return {
          modeType,
          multiplier: Number(multiplier),
          cost,
        };
      },
    );

    return {
      enabled: Boolean(this.config?.bonusBuy?.enabled),
      offers: entries,
    };
  }

  /**
   * Handle scatter retrigger during free spins
   *
   * @param {number} newScatterCount - Number of scatters that appeared
   */
  handleFreeSpinsRetrigger(newScatterCount) {
    if (this.bonusMode === null || !this.isInFreeSpins) {
      return;
    }

    let addedSpins = 0;

    if (newScatterCount >= 5 && !(this.bonusMode instanceof CascadeMaster)) {
      this._upgradeBonusMode("CASCADE_MASTER", 4);
      return;
    }

    if (newScatterCount >= 4 && this.bonusMode instanceof LockAndLoad) {
      this._upgradeBonusMode("GOLDEN_RUSH", 4);
      return;
    }

    if (newScatterCount >= 3) {
      addedSpins = 3;
    } else if (newScatterCount === 2) {
      addedSpins = 2;
    }

    this.bonusMode.remaining += addedSpins;
    this.freeSpinsRemaining += addedSpins;
  }

  _upgradeBonusMode(modeType, additionalSpins) {
    const remainingSpins = this.freeSpinsRemaining + additionalSpins;

    switch (modeType) {
      case "LOCK_AND_LOAD":
        this.bonusMode = new LockAndLoad(remainingSpins);
        break;
      case "GOLDEN_RUSH":
        this.bonusMode = new GoldenRush(remainingSpins);
        break;
      case "CASCADE_MASTER":
        this.bonusMode = new CascadeMaster(remainingSpins);
        break;
      default:
        throw new Error(`Unknown bonus mode: ${modeType}`);
    }

    this.freeSpinsRemaining = remainingSpins;
  }

  /**
   * Advance free spins counter and check if done
   *
   * @returns {boolean} True if free spins are now exhausted
   */
  advanceFreeSpins() {
    if (!this.isInFreeSpins) {
      return false;
    }

    if (this.bonusMode) {
      this.freeSpinsRemaining = this.bonusMode.remaining;
    }

    if (this.freeSpinsRemaining <= 0) {
      this.isInFreeSpins = false;
      this.bonusMode = null;
      return true; // Free spins complete
    }

    return false;
  }

  /**
   * Get current game state
   * @returns {Object} Complete game state for frontend
   */
  getState() {
    return {
      grid: this.currentGrid,
      totalWin: this.totalWinFromSpin,
      cascadeCount: this.cascadeCount,
      inFreeSpins: this.isInFreeSpins,
      freeSpinsRemaining: this.freeSpinsRemaining,
      bonusMode: this.bonusMode ? this.bonusMode.name : null,
      cascadeHistory: [], // Will be populated by renderer
      bonusFeatures: this.getBonusFeatureDisplay(),
    };
  }

  getBonusFeatureDisplay() {
    return {
      rainbowTriggered: this.rainbowTriggered,
      rainbowTargetSymbol: this.rainbowTargetSymbol,
      cloverSymbolsHit: [...this.cloverSymbolsHit],
      bucketSymbolsHit: [...this.bucketSymbolsHit],
      spinSpecialMultiplier: this.spinSpecialMultiplier,
      spinBucketMultiplier: this.spinBucketMultiplier,
      spinCollectionValue: this.spinCollectionValue,
      chainRoundsTriggered: this.chainRoundsTriggered,
      goldenSquaresCount: this.goldenSquares.size,
      bonusEventTimeline: this.bonusEventTimeline.map((round) => ({
        ...round,
        reveals: round.reveals.map((entry) => ({ ...entry })),
        cloverHits: round.cloverHits.map((entry) => ({
          ...entry,
          targets: entry.targets.map((target) => ({ ...target })),
        })),
        collectorSteps: round.collectorSteps.map((entry) => ({ ...entry })),
      })),
    };
  }

  /**
   * Get paytable for UI display
   * @returns {Object} Symbol payouts and feature info
   */
  getPaytable() {
    return {
      symbols: LUCKY_ESCAPE_CONFIG.symbols,
      basePayouts: CascadeDetector.BASE_PAYOUTS,
      clusterMultipliers: CascadeDetector.CLUSTER_MULTIPLIERS,
      scatterTriggers: {
        3: "Lock & Load (10 free spins)",
        4: "Golden Rush (12 free spins)",
        5: "Cascade Master (12 free spins)",
      },
      maxWin: this.config.maxWin,
      rtp: this.config.rtp,
    };
  }

  /**
   * Create grid from reel positions (for future multi-reel support)
   * Currently assumes 5x5 grid
   * @private
   */
  _generateRandomGrid() {
    // Generate a random 5x5 grid with symbol weights
    const grid = CascadeEngine.createEmptyGrid();

    for (let y = 0; y < 5; y++) {
      for (let x = 0; x < 5; x++) {
        grid[y][x] = this._generateSymbol();
      }
    }

    return grid;
  }

  /**
   * Generate a random symbol (respecting weights)
   * @private
   */
  _generateSymbol() {
    const weights = [
      { id: 1, weight: 30 }, // RED
      { id: 2, weight: 25 }, // GREEN
      { id: 3, weight: 20 }, // PURPLE
      { id: 4, weight: 15 }, // YELLOW
      { id: 5, weight: 7 }, // BLUE
      { id: 6, weight: 3 }, // WILD
      { id: 7, weight: 2 }, // SCATTER
    ];

    const totalWeight = weights.reduce((sum, w) => sum + w.weight, 0);
    let roll = this.rng.nextInt(0, totalWeight - 1);

    for (const symbol of weights) {
      roll -= symbol.weight;
      if (roll < 0) {
        return symbol.id;
      }
    }

    return weights[0].id;
  }

  _resetSpinFeatureState() {
    this.rainbowTargetSymbol = null;
    this.rainbowTriggered = false;
    this.cloverSymbolsHit = [];
    this.bucketSymbolsHit = [];
    this.spinSpecialMultiplier = 1;
    this.spinBucketMultiplier = 1;
    this.spinCollectionValue = 0;
    this.chainRoundsTriggered = 0;
    this.bonusEventTimeline = [];
  }

  _isBanditStyleBonusActive() {
    return this.isInFreeSpins;
  }

  _prepareBonusActivationGrid() {
    const rainbowPositions = [];

    for (let y = 0; y < 5; y++) {
      for (let x = 0; x < 5; x++) {
        if (this.currentGrid[y][x] === 9) {
          rainbowPositions.push({ x, y });
        }
      }
    }

    const guaranteedRainbow = this.bonusMode instanceof CascadeMaster;
    const rainbowChance = this.bonusMode instanceof GoldenRush ? 0.3 : 0.22;

    const shouldPlaceRainbow =
      guaranteedRainbow ||
      rainbowPositions.length > 0 ||
      this.rng.nextFloat() < rainbowChance;

    if (!shouldPlaceRainbow) {
      return;
    }

    if (rainbowPositions.length === 0) {
      const slot = this._pickRandomRegularPosition();
      if (slot) {
        this.currentGrid[slot.y][slot.x] = 9;
      }
    }

    this._resolveGoldenSquareActivation();
  }

  _trackGoldenSquaresFromWins(winResult) {
    for (const cluster of winResult.clusters) {
      for (const pos of cluster.positions) {
        this.goldenSquares.add(`${pos.x},${pos.y}`);
      }
    }
  }

  _resolveGoldenSquareActivation() {
    if (this.goldenSquares.size === 0) {
      return;
    }

    const rainbowPositions = [];
    for (let y = 0; y < 5; y++) {
      for (let x = 0; x < 5; x++) {
        if (this.currentGrid[y][x] === 9) {
          rainbowPositions.push({ x, y });
        }
      }
    }

    if (rainbowPositions.length === 0) {
      return;
    }

    this.rainbowTriggered = true;
    this.rainbowTargetSymbol = this.rng.nextInt(1, 5);
    this._applyRainbowTransform(this.rainbowTargetSymbol);

    const rounds = this._runGoldenSquareChainRounds();
    this.chainRoundsTriggered = rounds;

    if (this.bonusMode instanceof LockAndLoad) {
      this.goldenSquares.clear();
    }
  }

  _runGoldenSquareChainRounds() {
    const maxRounds = 2;
    let roundsExecuted = 0;
    let continueRounds = true;

    while (continueRounds && roundsExecuted < maxRounds) {
      roundsExecuted++;
      const roundResult = this._executeSingleChainRound();

      this.spinCollectionValue += roundResult.roundCollectionValue;

      continueRounds = roundResult.bucketCount > 0;
    }

    return roundsExecuted;
  }

  _executeSingleChainRound() {
    const coinValues = new Map();
    const bucketPositions = [];
    const cloverValues = [];
    const bucketMultipliers = new Map();
    const rollChances = this._getGoldenSquareOutcomeChances();
    const roundEvents = {
      reveals: [],
      cloverHits: [],
      collectorSteps: [],
      roundCollectionValue: 0,
    };

    for (const key of this.goldenSquares) {
      const [x, y] = key.split(",").map(Number);
      const roll = this.rng.nextFloat();

      if (roll < rollChances.coin) {
        const coinValue = this._rollCoinValue();
        coinValues.set(key, coinValue);
        roundEvents.reveals.push({
          x,
          y,
          type: "coin",
          value: coinValue,
          tier: this._getCoinTier(coinValue),
        });
        continue;
      }

      if (roll < rollChances.coin + rollChances.clover) {
        const cloverMultiplier = this._rollCloverMultiplierValue();
        cloverValues.push({ x, y, value: cloverMultiplier });
        this.cloverSymbolsHit.push(cloverMultiplier);
        this.currentGrid[y][x] = 8;
        roundEvents.reveals.push({
          x,
          y,
          type: "clover",
          value: cloverMultiplier,
        });
        continue;
      }

      bucketPositions.push({ x, y, key });
      bucketMultipliers.set(key, 1);
      this.bucketSymbolsHit.push(1);
      this.currentGrid[y][x] = 10;
      roundEvents.reveals.push({
        x,
        y,
        type: "collector",
        value: 1,
      });
    }

    const adjustedCoinValues = new Map(coinValues);
    for (const clover of cloverValues) {
      const cloverTargets = [];

      for (let dy = -1; dy <= 1; dy++) {
        for (let dx = -1; dx <= 1; dx++) {
          if (dx === 0 && dy === 0) {
            continue;
          }

          const tx = clover.x + dx;
          const ty = clover.y + dy;
          if (tx < 0 || tx >= 5 || ty < 0 || ty >= 5) {
            continue;
          }

          const targetKey = `${tx},${ty}`;
          if (adjustedCoinValues.has(targetKey)) {
            const beforeValue = adjustedCoinValues.get(targetKey);
            adjustedCoinValues.set(
              targetKey,
              beforeValue * clover.value,
            );

            cloverTargets.push({
              x: tx,
              y: ty,
              type: "coin",
              before: beforeValue,
              after: beforeValue * clover.value,
            });
          }

          if (bucketMultipliers.has(targetKey)) {
            const beforeMultiplier = bucketMultipliers.get(targetKey);
            bucketMultipliers.set(
              targetKey,
              beforeMultiplier * clover.value,
            );

            cloverTargets.push({
              x: tx,
              y: ty,
              type: "collector",
              before: beforeMultiplier,
              after: beforeMultiplier * clover.value,
            });
          }
        }
      }

      if (cloverTargets.length > 0) {
        roundEvents.cloverHits.push({
          x: clover.x,
          y: clover.y,
          multiplier: clover.value,
          targets: cloverTargets,
        });
      }
    }

    bucketPositions.sort((a, b) => (a.y === b.y ? a.x - b.x : a.y - b.y));
    const bucketValues = new Map();

    let runningTotal = Array.from(adjustedCoinValues.values()).reduce(
      (sum, value) => sum + value,
      0,
    );

    for (const bucket of bucketPositions) {
      const multiplier = bucketMultipliers.get(bucket.key) || 1;
      const collectedBeforeMultiplier = runningTotal;
      const bucketValue = collectedBeforeMultiplier * multiplier;
      bucketValues.set(bucket.key, bucketValue);
      runningTotal += bucketValue;

      roundEvents.collectorSteps.push({
        x: bucket.x,
        y: bucket.y,
        multiplier,
        collectedBeforeMultiplier,
        collectedValue: bucketValue,
        runningTotalAfter: runningTotal,
      });
    }

    const roundCollectionValue = Math.min(
      10000,
      Array.from(adjustedCoinValues.values()).reduce(
        (sum, value) => sum + value,
        0,
      ) +
        Array.from(bucketValues.values()).reduce(
          (sum, value) => sum + value,
          0,
        ),
    );

      roundEvents.roundCollectionValue = roundCollectionValue;
      roundEvents.bucketCount = bucketPositions.length;
      this.bonusEventTimeline.push(roundEvents);

    return {
      bucketCount: bucketPositions.length,
      roundCollectionValue,
    };
  }

  _getGoldenSquareOutcomeChances() {
    if (this.bonusMode instanceof CascadeMaster) {
      return { coin: 0.84, clover: 0.12, bucket: 0.04 };
    }

    if (this.bonusMode instanceof GoldenRush) {
      return { coin: 0.86, clover: 0.11, bucket: 0.03 };
    }

    return { coin: 0.89, clover: 0.09, bucket: 0.02 };
  }

  _pickRandomRegularPosition() {
    const candidates = [];

    for (let y = 0; y < 5; y++) {
      for (let x = 0; x < 5; x++) {
        const symbol = this.currentGrid[y][x];
        if (symbol >= 1 && symbol <= 5) {
          candidates.push({ x, y });
        }
      }
    }

    if (candidates.length === 0) {
      return null;
    }

    const selectedIndex = this.rng.nextInt(0, candidates.length - 1);
    return candidates[selectedIndex];
  }

  _rollCoinValue() {
    const table = [
      { value: 0.2, weight: 18 },
      { value: 0.5, weight: 16 },
      { value: 1, weight: 14 },
      { value: 2, weight: 12 },
      { value: 3, weight: 9 },
      { value: 4, weight: 8 },
      { value: 5, weight: 7 },
      { value: 7, weight: 4 },
      { value: 10, weight: 4 },
      { value: 15, weight: 3 },
      { value: 20, weight: 2 },
      { value: 50, weight: 1 },
      { value: 100, weight: 1 },
      { value: 200, weight: 1 },
      { value: 500, weight: 1 },
    ];

    const total = table.reduce((sum, item) => sum + item.weight, 0);
    let roll = this.rng.nextInt(0, total - 1);

    for (const item of table) {
      roll -= item.weight;
      if (roll < 0) {
        return item.value;
      }
    }

    return 0.2;
  }

  _getCoinTier(value) {
    if (value >= 25) {
      return "gold";
    }

    if (value >= 5) {
      return "silver";
    }

    return "bronze";
  }

  _applyRainbowTransform(targetSymbolId) {
    for (let y = 0; y < 5; y++) {
      for (let x = 0; x < 5; x++) {
        if (this.currentGrid[y][x] === targetSymbolId) {
          this.currentGrid[y][x] = 6; // WILD
        }
      }
    }
  }

  _rollCloverMultiplierValue() {
    const table = [
      { multiplier: 2, weight: 38 },
      { multiplier: 3, weight: 26 },
      { multiplier: 4, weight: 15 },
      { multiplier: 5, weight: 10 },
      { multiplier: 6, weight: 6 },
      { multiplier: 8, weight: 3 },
      { multiplier: 10, weight: 2 },
    ];

    const total = table.reduce((sum, item) => sum + item.weight, 0);
    let roll = this.rng.nextInt(0, total - 1);

    for (const item of table) {
      roll -= item.weight;
      if (roll < 0) {
        return item.multiplier;
      }
    }

    return 2;
  }

  _filterLockedWinPositions(winPositions) {
    const filtered = new Set();

    for (const key of winPositions) {
      const normalized = key.replace("_", ",");
      if (!this.bonusMode.isLocked(...normalized.split(",").map(Number))) {
        filtered.add(key);
      }
    }

    return filtered;
  }

  _getWinningPositionMultiplier(winResult) {
    if (
      !this.isInFreeSpins ||
      !this.bonusMode ||
      !winResult?.clusters?.length
    ) {
      return 1;
    }

    const positionMultipliers = [];

    for (const cluster of winResult.clusters) {
      for (const pos of cluster.positions) {
        positionMultipliers.push(this._getPositionMultiplier(pos.x, pos.y));
      }
    }

    if (positionMultipliers.length === 0) {
      return 1;
    }

    const total = positionMultipliers.reduce((sum, value) => sum + value, 0);
    return total / positionMultipliers.length;
  }

  _getPositionMultiplier(x, y) {
    if (this.bonusMode instanceof LockAndLoad) {
      return this.bonusMode.isLocked(x, y)
        ? this.bonusMode.lockedMultiplier
        : 1;
    }

    if (this.bonusMode instanceof GoldenRush) {
      return this.bonusMode.getMultiplier(x, y);
    }

    return 1;
  }
}

export default LuckyScapeSlot;
