/**
 * Lock & Load Bonus Mode
 *
 * Tier role in current implementation:
 * - Base free-spin tier (triggered by 3 scatters)
 * - Starts with 10 free spins
 * - Uses the shared Golden-Square chain engine from LuckyScapeSlot
 * - This class still provides legacy lock tracking/multiplier hooks used by renderer/state APIs
 */

import { BonusMode } from "./bonusMode.js";

export class LockAndLoad extends BonusMode {
  constructor(initialSpins = 10) {
    super(initialSpins);
    this.name = "Lock & Load";
    this.lockedPositions = new Map(); // Map of "x,y"  symbol ID
    this.lockedMultiplier = 1.5;
    this.maxRetriggers = 2;
    this.retriggersUsed = 0;
  }

  /**
   * Apply lock to winning positions
   * Lock is persistent for rest of bonus
   */
  onCascade(cascadeIndex, grid, winResult) {
    // Lock all winning positions
    for (const cluster of winResult.clusters) {
      for (const pos of cluster.positions) {
        const key = `${pos.x},${pos.y}`;
        this.lockedPositions.set(key, grid[pos.y][pos.x]);
      }
    }

    // Return base multiplier (locks themselves provide 1.5x)
    return 1.0;
  }

  /**
   * Retriggers are handled in LuckyScapeSlot
   * Keep method for interface compatibility
   */
  onRetrigger(newScatterCount) {
    return 0;
  }

  /**
   * Get locked positions for renderer
   */
  getLockedPositions() {
    return this.lockedPositions;
  }

  /**
   * Check if position is locked
   */
  isLocked(x, y) {
    const key = `${x},${y}`;
    return this.lockedPositions.has(key);
  }

  /**
   * Get display data with lock status
   */
  getDisplayData() {
    return {
      ...super.getDisplayData(),
      lockedCount: this.lockedPositions.size,
      lockedMultiplier: this.lockedMultiplier,
      retriggersRemaining: this.maxRetriggers - this.retriggersUsed,
    };
  }
}

export default LockAndLoad;
