/**
 * Cascade Master Bonus Mode
 *
 * Tier role in current implementation:
 * - Top free-spin tier (triggered by 5+ scatters)
 * - Starts with 12 free spins
 * - Uses the shared Golden-Square chain engine from LuckyScapeSlot
 * - Guarantees Rainbow activation each free spin (handled in slot-level chain logic)
 * - This class keeps cascade tracking fields for compatibility with display/state interfaces
 */

import { BonusMode } from "./bonusMode.js";

export class CascadeMaster extends BonusMode {
  constructor(initialSpins = 12) {
    super(initialSpins);
    this.name = "Cascade Master";
    this.currentCascadeMultiplier = 1; // Resets every spin
    this.maxCascades = 0; // Track max cascades in current spin
    this.maxRetriggers = 2;
    this.retriggersUsed = 0;
    this.cascadeHistory = []; // Track cascade multipliers per spin
  }

  /**
   * Increment cascade multiplier and apply to wins
   * Each cascade multiplies results by cascade count
   */
  onCascade(cascadeIndex, grid, winResult) {
    // Update cascade multiplier based on cascade index
    // cascadeIndex is 1-based
    let multiplier = cascadeIndex;

    // Spin 7 bonus: start cascade multiplier at 2x
    if (this.spinsCompleted === 6) {
      // 7th spin (0-indexed)
      multiplier = Math.max(2, multiplier);
    }

    // Cap multiplier at 4x
    multiplier = Math.min(multiplier, 4);
    this.currentCascadeMultiplier = multiplier;
    this.maxCascades = Math.max(this.maxCascades, cascadeIndex);

    // Return the cascade multiplier for this cascade
    // Only the final cascade applies to wins
    return cascadeIndex === 1 ? 1.0 : multiplier;
  }

  /**
   * Record cascade history when spin completes
   */
  onSpinComplete(spinWin) {
    // Record max cascade multiplier achieved this spin
    if (this.maxCascades > 0) {
      this.cascadeHistory.push({
        spin: this.spinsCompleted + 1,
        cascadeCount: this.maxCascades,
        maxMultiplier: this.currentCascadeMultiplier,
        winAmount: spinWin,
      });
    }

    // Reset for next spin
    this.currentCascadeMultiplier = 1;
    this.maxCascades = 0;

    // Call parent to track overall stats
    return super.onSpinComplete(spinWin);
  }

  /**
   * Retriggers are handled in LuckyScapeSlot
   * Keep method for interface compatibility
   */
  onRetrigger(newScatterCount) {
    return 0;
  }

  /**
   * Get current cascade multiplier
   */
  getCurrentMultiplier() {
    return this.currentCascadeMultiplier;
  }

  /**
   * Get cascade history for spin summary
   */
  getCascadeHistory() {
    return this.cascadeHistory;
  }

  /**
   * Check if spin 7 bonus applies
   */
  isSpin7Bonus() {
    return this.spinsCompleted === 6; // 0-indexed, so 6 = 7th spin
  }

  /**
   * Get display data with cascade status
   */
  getDisplayData() {
    const lastSpin = this.cascadeHistory[this.cascadeHistory.length - 1];

    return {
      ...super.getDisplayData(),
      currentMultiplier: this.currentCascadeMultiplier,
      maxCascadesThisSpin: this.maxCascades,
      cascadeHistory: this.cascadeHistory,
      lastSpinCascades: lastSpin ? lastSpin.cascadeCount : 0,
      lastSpinMultiplier: lastSpin ? lastSpin.maxMultiplier : 1,
      spin7BonusActive: this.isSpin7Bonus(),
      retriggersRemaining: this.maxRetriggers - this.retriggersUsed,
    };
  }
}

export default CascadeMaster;
